#mainNav {
  height: 70px;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 1) 0%,
    rgba(75, 72, 87, 0.877) 100%
  );
  color: #949494;
  border-radius: 0 0 10px 10px;
  -webkit-animation: scale-in-ver-top 0.4s ease-in-out alternate both;
          animation: scale-in-ver-top 0.4s ease-in-out alternate both;
}
#langSelect {
  background: none;
  border: none;
  color: #bbbaba;
}
#mainNav .navbar-toggler {
  font-size: 120%;
  padding: 0.75rem;
  color: #8f8f8f;
  border: 1px solid #64a19d;
}

#mainNav .navbar-toggler:focus {
  outline: none;
}
#mainNav .nav-link {
  color: #e0e2e2;
  font-size: 15px;
}

#mainNav .navbar-brand {
  color: #e0e0e0;
  font-weight: 500;
  padding: 0.9rem 0;
}

#mainNav .navbar-nav .nav-item:hover {
  color: (fade (rgb(255, 255, 255) 55, 255, 255), 80%);
  outline: none;
}
#cvEmbed {
  display: none;
}

.projects-section .project-text hr {
  border-color: rgb(113, 60, 236);
  border-width: 0.25rem;
  width: 30%;
}

#mainNav .navbar-nav .nav-item:active,
#mainNav .navbar-nav .nav-item:focus {
  outline: none;
  background-color: transparent;
}
@media (max-width: 524px) {
  #myname {
    display: none;
  }
  #cvEmbed {
    display: none;
  }
  #mainNav ul {
    margin-right: 1vw;
  }
  .projects-section .project-text hr {
    border-color: rgb(113, 60, 236);
    border-width: 0.25rem;
    width: 30%;
  }
}

@media (min-width: 992px) {
  .img-fluid {
    height: 280px !important;
  }
  #mainNav {
    height: 40px;
    padding-top: 0;
    padding-bottom: 0;
    border-bottom: none;
    background: linear-gradient(
      to right,
      rgba(0, 0, 0, 1) 0%,
      rgba(75, 72, 87, 0.877) 100%
    );
    transition: background-color 0.3s ease-in-out;
    -webkit-animation: scale-in-ver-top 0.4s ease-in-out alternate both;
            animation: scale-in-ver-top 0.4s ease-in-out alternate both;
  }

  #mainNav .navbar-brand {
    padding: 0.5rem 0;
    color: rgb(184, 184, 184);
  }
  #mainNav .nav-link {
    transition: none;
    padding: 2rem 1.5rem;
    color: rgb(206, 200, 200);
  }
  #mainNav .nav-link:hover {
    color: whitesmoke;
  }
  #mainNav .nav-link:active {
    color: white;
  }
  #mainNav.navbar-shrink {
    background-color: linear-gradient(
      to right,
      rgba(0, 0, 0, 1) 0%,
      rgba(45, 45, 45, 0.473) 100%
    );
  }
  #mainNav.navbar-shrink .navbar-brand {
    color: #bbbaba;
  }
  #mainNav.navbar-shrink .nav-link {
    color: #706d6d;
    padding: 1.5rem 1.5rem 1.25rem;
    border-bottom: 0.25rem solid transparent;
  }
  #mainNav.navbar-shrink .nav-link:hover {
    color: whitesmoke;
  }
  #mainNav.navbar-shrink .nav-link:active {
    color: #467370;
  }
  #mainNav.navbar-shrink .nav-link.active {
    color: #64a19d;
    outline: none;
    border-bottom: 0.25rem solid #64a19d;
  }
}

.masthead {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 35rem;
  padding: 15rem 0;
  background: url(/portfolio/static/media/indexBG.c5659bdb.png);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
#gitContainer {
  margin-bottom: 5rem !important;
}
.masthead h1 {
  font-family: "Varela Round";
  font-size: 2rem;
  line-height: 2.5rem;
  letter-spacing: 0.8rem;
  background: -webkit-linear-gradient(
    rgba(255, 255, 255, 0.9),
    rgba(255, 255, 255, 0)
  );
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
}
#title {
  animation: text-blur-out 1.2s cubic-bezier(0.55, 0.085, 0.68, 0.53) 1s reverse
    both;
}
@-webkit-keyframes text-blur-out {
  0% {
    -webkit-filter: blur(0.01);
            filter: blur(0.01);
  }
  100% {
    -webkit-filter: blur(12px) opacity(0);
            filter: blur(12px) opacity(0);
  }
}
@keyframes text-blur-out {
  0% {
    -webkit-filter: blur(0.01);
            filter: blur(0.01);
  }
  100% {
    -webkit-filter: blur(12px) opacity(0);
            filter: blur(12px) opacity(0);
  }
}
.masthead h2 {
  max-width: 20rem;
  font-size: 1rem;
  animation: text-blur-out 2s cubic-bezier(0.55, 0.085, 0.68, 0.53) 2s reverse
    both;
}

@media (min-width: 768px) {
  .masthead h1 {
    font-size: 3rem;
    line-height: 4rem;
  }
}

@media (min-width: 992px) {
  .masthead {
    height: 100vh;
    padding: 0;
  }
  .masthead h1 {
    font-size: 4rem;
    line-height: 6.5rem;
    letter-spacing: 0.8rem;
  }
  .masthead h2 {
    max-width: 30rem;
    font-size: 1.25rem;
  }
}

